<template>
  <div class="page bgc-white">
    <div class="content">
      <div class="banner" v-lazy:background-image="service.imgUrl.op_bpic"></div>
      <div class="bgc-white p15" v-html="service.service_content"></div>
    </div>
    <div class="btn-wrap box">
      <a :href="'tel:' + service.service_tel" class="btn btn-round box-center call">
        <img width="30" src="@/assets/images/industryUnion/phone.png" alt=""></a>
      <router-link :to="{name: 'serviceSubmit', query: query}" class="btn btn-success btn-round bf1">
        申请服务
      </router-link>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'serviceItem',
    data() {
      return {
        userInfo: '',
        query: {},
        service: {
          imgUrl: {}
        }
      }
    },
    created() {
      this.query = this.$route.query
      if (utils.isEmpty(this.query)) {
        this.$router.push({
          name: 'Index'
        })
        return
      }
      this.getUserInfo()
      this.pullData()
    },
    methods: {
      getUserInfo() {
        this.axios.post(this.apiConfig.getUserInfo, {}).then(res => {
          if (res.s === 1) {
            this.userInfo = res.d
          } else {
            this.$router.push({
              name: 'Index'
            })
          }
        })
      },
      pullData() {
        let id = utils.getQueryString(window.location.href, 'id')
        if (!id) {
          return
        }
        this.axios.get(this.apiConfig.unionServiceDetail, {
          params: {id}
        }).then(res => {
          if (res.s === 1) {
            this.$store.dispatch('UPDATE_LOADINGSTATE', false)
            this.service = res.d
            document.title = res.d.service_title
          }
        })
      }
    }
  }
</script>
<style scoped lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  .call {
    width: rem(50px);
    height: rem(50px);
    margin-right: rem(10px);
    img {
      width: rem(30px);
    }
  }
  .banner {
    width: 100%;
    height: rem(140px);
    background: url('../../../assets/images/500.png') center no-repeat;
    background-size: 100%;
  }
</style>
